import React from "react";
import ProjectCard from "../Home/ProjectCard";

const ActiveProjects = () => {
  return (
    <div>
      <h1 className="text-center">Active Projects</h1>
      <ProjectCard
        title="Board Game Points Counter (Backend)"
        subtitle="Python, Django, Django Rest Framework, Django Channels, PostgreSQL"
        desc="App that allows to create rooms for couting points in board games. Create custom game schemes (e.g. gold, health, level). Create rooms from scheme and add your friends."
        className="mb-3"
      />
      <ProjectCard
        title="Board Game Points Counter (Web Frontend)"
        subtitle="NodeJS, React, Redux, React Beautiful DND, React Router, Styled Components"
        desc="Web based frontend for BGPC."
      />
    </div>
  );
};

export default ActiveProjects;
